@import url(https://fonts.googleapis.com/css?family=Quicksand:400,600,700&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: black;
  height: 100%;
  font-family: "Quicksand";
}

.movie {
  display: inline-block;
  color: white;
  padding: 20px 15px;
  overflow: hidden;
  background-color: rgb(25, 25, 25);
  border: 4px solid #feeea6;
  border-radius: 15px;
  margin-bottom: 30px;
  width: 600px;
  margin-right: 30px;
}

.poster-container {
  display: flex;
  justify-content: space-between;
}
.movie__movie-year {
  margin-top: 10px;
}
.movie__movie-summary {
  margin-left: 30px;
}

.movie__movie-poster {
  width: 230px;
  height: 350px;
  border: 1px solid white;
  border-radius: 10px;
}
ul {
  list-style: none;
  display: flex;
  padding-left: 0;
}

li {
  margin-right: 15px;
  font-weight: 400;
  font-size: 20px;
}

.movie__movie-year {
  font-size: 15px;
  font-weight: 400;
}
.movie p {
  font-weight: 400;
  font-size: 15px;
}

.movie:hover {
  -webkit-animation: pump 0.2s ease-in-out forwards;
          animation: pump 0.2s ease-in-out forwards;
}

@-webkit-keyframes pump {
  0% {
    -webkit-transform: none;
            transform: none;
  }
  100% {
    -webkit-transform: translateY(-1%);
            transform: translateY(-1%);
  }
}

@keyframes pump {
  0% {
    -webkit-transform: none;
            transform: none;
  }
  100% {
    -webkit-transform: translateY(-1%);
            transform: translateY(-1%);
  }
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

h2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.loader__text {
  color: white;
}

